$(function () {
	let header = $('.header');
	let hederHeight = header.height(); //   

	$(window).scroll(function () {
		//    
		if ($(this).scrollTop() > 1) {
			header.addClass('header-fixed');
			$('body').css({
				'paddingTop': hederHeight + 'px' //    body,   
			});
		} else {
			header.removeClass('header-fixed');
			$('body').css({
				'paddingTop': 0 //    body,   
			})
		}
		//  ,     
		if ($(this).scrollTop() > 300) {
			header.css({
				'padding': '5px 0',
				'background': '#ebebeb',
				'transition': '.3s'
			});
		} else {
			header.css({
				'padding': '15px 0',
				'background': '#fff',
				'transition': '.3s'
			});
		}
	});

});